import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { of } from 'rxjs/observable/of';
import { _throw as throwError } from 'rxjs/observable/throw';
import { flatMap, map } from 'rxjs/operators';
import { FileType } from '../../enums/file-type';
import { VDependent } from '../../model/vDependent';
import { DateUtil } from '../../utils/date.util';
import { ConfigService } from '../config/config.service';
import { VActivityResponse } from '../vActivityResponse';
import { VDocument, VDocumentResponse } from './vDocumentResponse';
import { VDocumentInfo } from './vDocumentUpdateRequest';

@Injectable()
export class DocumentService {

  constructor(private http: HttpClient, private config: ConfigService, private dateUtil: DateUtil) {
  }

  convert(document: VDocumentResponse): VDocument {
    const response: VDocument = {
      id: document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.mediaId : '',
      title: document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.documentTitle : '',
      notes: document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.documentTitle : '',
      author: document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.documentTitle : '',
      appointmentId: document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.appointmentId : '',
      mimeType:  document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.mimeType : '',
      dependentId: document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.individualId : '',
      created: new Date(),
      type: this.convertFileType(document && document.responseBody && document.responseBody[0].body ? document.responseBody[0].body.mimeType : '')
    };

    if (document && document.fileContent && document.fileContent[0] && document.fileContent[0].body) {
      const byteCharacters = window.atob((document.fileContent[0].body));
      //TODO: convert from application/octet-stream to byte array 

      const byteNumbers = new Array(byteCharacters.length);
      for (let i = 0; i < byteCharacters.length; i++) {
          byteNumbers[i] = byteCharacters.charCodeAt(i);
      }

      const byteArray = new Uint8Array(byteNumbers);

      response.file = DocumentService.convertFromByteArray(byteArray, document.responseBody[0].body.mimeType);
      
    } else{
      response.file = new Observable<Blob>();
    }


    return response;
  }

  /**
   * Calculate the document type from a mime type.
   */
  convertFileType(mimeType: string): FileType {
    if(mimeType) mimeType = mimeType.toLowerCase();
    switch (mimeType) {
      case 'image/jpeg':
      case 'image/png':
      case 'jpg':
      case 'png':
        return FileType.IMAGE;
      case 'doc':
      case 'docx':
      case 'txt':
      case 'application/msword':
      case 'application/vnd.openxmlformats-officedocument.wordprocessingml.document':
        return FileType.DOCUMENT;
      case 'xls':
      case 'xlsx':
      case 'csv':
      case 'application/vnd.ms-excel':
      case 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet':
      case 'text/csv':
        return FileType.SPREADSHEET;
      case 'application/pdf':
      case 'pdf':
        return FileType.PDF;
      default:
        return null;
    }

  }

  /**
   * Fetch all documents for a dependent
   */
  fetchDocuments(dependentId: string): Observable<VDocument[]> {
    return this.http.get<{ documents: VDocumentResponse[] }>(`${this.config.get.apiUrl}/vhealth_members/${dependentId}/documents`)
      .pipe(map(response => response.documents.map(document => this.convert(document))));
  }

  /**
   * Fetch a specific document by ID
   */
  getDocument(documentId: string, dependentId: string): Observable<VDocument> {
    return this.http.get<VDocumentResponse>(`${this.config.get.apiUrl}/vhealth_members/${dependentId}/documents/${documentId}`)
      .pipe(map(response => this.convert(response)));
  }

  /**
   * Upload a new document.
   */
  uploadDocument(file: File, info: VDocumentInfo = {}, dependent: VDependent, appointmentId?: string): Observable<VDocument> {

    if (!this.convertFileType(file.name.split('.').pop())) {
      return throwError('Invalid mime type');
    }

    const formData = new FormData();

    const params = {
      notes: info.notes,
      appointmentId,
      documentTitle: info.title+'.'+file.name.split('.').pop().toLowerCase(),
      createdTime: this.dateUtil.formatAPIDate(new Date()),
      individualId: String(dependent.id),
      documentAuthor: dependent.firstName + ' ' + dependent.lastName,
      mimeType: file.name.split('.').pop().toLowerCase(),
      activity: 'ADD'
    };


    //prepare formData that is to be sent across
    formData.append("requestBody", JSON.stringify(params));
    formData.append("fileContent", file);
    const httpOptions = {
      headers: new HttpHeaders({
          'enctype': 'multipart/form-data; boundary=----WebKitFormBoundaryuL67FWkv1CA'
      })
    };
   return (this.http.post<{ mediaId: string }>(`${this.config.get.apiUrl}/vhealth_documents/upload`, formData, httpOptions))
   .pipe(flatMap(({mediaId}) => this.getDocument(mediaId, dependent.id)));

  }

  /**
   * Remove a specific document
   */
  removeDocument(documentId: string, dependent: VDependent): Observable<VActivityResponse> {
    return this.http.delete<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_members/${dependent.id}/documents/${documentId}`);
  }

  /**
   * Update details of ane existing document.
   */
  updateDocument(documentId: string, info: VDocumentInfo): Observable<VActivityResponse> {

    const params = {
      mediaId: documentId,
      documentTitle: info.title,
      notes: info.notes,
      activity: 'UPDATE'
    };

    return this.http.post<VActivityResponse>(`${this.config.get.mockUrl}/documents/activity`, JSON.stringify(params));
  }

  /**
   * Convert a document from a byte array into a file
   */
  static convertFromByteArray(array: Uint8Array, mimeType: string): Observable<Blob> {

    const signedArray = new Int8Array(array);
    return of(new Blob([signedArray], {type: mimeType}));

  }

  /**
   * Convert a file into a byte array for posting to the API
   */
  static convertToByteArray(blob: Blob): Observable<number[]> {

    const reader = new FileReader();

    return new Observable(observer => {

      reader.onerror = observer.error;
      reader.onload = () => {
        observer.next(Array.from(new Int8Array(reader.result)));
      };
      reader.readAsArrayBuffer(blob);

    });

  }
}
