import { HttpClient, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from "rxjs/Observable";
import { map } from 'rxjs/operators';
import { CommunicationType } from "../../enums/communication-type";
import { ConsentType } from "../../enums/consent-type";
import { ConfigService } from "../config/config.service";
import { VCommunicationReference, VCommunicationResponse, VConsent } from "./vCommunicationResponse";
import { VConsentConfig } from "./vConsentConfig";
import { VConsentResponse } from "./vConsentResponse";


@Injectable()
export class ConsentService {

  constructor(private http: HttpClient, private config: ConfigService) {
  }

  getTermsAndConditions(dependentID: string): Observable<VConsentResponse>  {
    const httpParams = new HttpParams();
    httpParams.set('key', ConsentType.tandc);
    httpParams.set('version', '1');

    return this.http.get<VConsentResponse>(`${this.config.get.apiUrl}/members/${dependentID}/communicationpreferences`, {params: httpParams})
      .pipe(map(response => response));
  }

  private convertConsent(response: VCommunicationResponse[]):VConsentConfig {
    const arr = response.map(config => {
      const comms = config.communicationReference.map(communication => communication.communicationType);
      return {
        key: config.key,
        isSelected: comms.length > 0,
        phone: comms.indexOf(CommunicationType.phone) > -1,
        sms: comms.indexOf(CommunicationType.sms) > -1,
        email: comms.indexOf(CommunicationType.email) > -1,
      };
    });

    const marketingOptions = arr.find(option => option.key === ConsentType.marketing);
    const researchOptions = arr.find(option => option.key === ConsentType.research);

    return {
      marketing: {
        isSelected: marketingOptions && marketingOptions.isSelected ? marketingOptions.isSelected : false,
        phone: marketingOptions && marketingOptions.phone ? marketingOptions.phone : false,
        sms: marketingOptions && marketingOptions.sms ? marketingOptions.sms : false,
        email: marketingOptions && marketingOptions.email ? marketingOptions.email : false,
      },
      research: {
        isSelected: researchOptions && researchOptions.isSelected ? researchOptions.isSelected : false,
        sms: researchOptions && researchOptions.sms ? researchOptions.sms : false,
        email: researchOptions && researchOptions.email ? researchOptions.email : false,
        phone: researchOptions && researchOptions.phone ? researchOptions.phone : false,
      }
    };
  }

  private mapConsentParams(consent: VConsentConfig):VCommunicationResponse[] {

    return Object.entries(consent).map(item => {
      const config = item[1];
      const comms: VCommunicationReference[] = [];

      // only add if they have opted in
      if (config.isSelected) {

        Object.entries(config).map(commItem => {
          // tslint:disable-next-line:no-any
            if(CommunicationType[(commItem[0] as any)]) comms.push({communicationType: CommunicationType[(commItem[0] as any)]});
        });
        return {
          consentStatus: 'Y',
          // tslint:disable-next-line:no-any
          key: ConsentType[(item[0] as any)],
          communicationReference: comms
        };
      }
      return null;
    });
  }

  retrieveConsent(dependentID: string): Observable<VCommunicationResponse>{
    return this.http.get<VCommunicationResponse>(`${this.config.get.apiUrl}/members/${dependentID}/communicationpreferences`)
      .pipe(map(response => response));
  }

  addConsent(params: VCommunicationResponse[], dependentID: string): Observable<VConsent>{
    return this.http.post<VConsent>(`${this.config.get.apiUrl}/members/${dependentID}/communicationpreferences`, JSON.stringify({ consents: params }))
      .pipe(map(response => response));
  }

  getConsent(key: ConsentType): Observable<VConsentResponse>  {

    const httpParams = new HttpParams();
    httpParams.set('key', key);
    httpParams.set('version', '1');
    return this.http.get<VConsentResponse>(`${this.config.get.apiUrl}/useragreement`, {params: httpParams})
      .pipe(map(response => response));
  }

  addCommsConsent(params: VConsentConfig, dependentID: string): Observable<VConsent>{
    const consentArray = this.mapConsentParams(params).filter(res => res);
    return this.http.post<VConsent>(`${this.config.get.apiUrl}/members/${dependentID}/communicationpreferences`, JSON.stringify({ consents: consentArray }))
      .pipe(map(response => response));
  }

  getTermsAndConditionsConsent(dependentID: string): Observable<boolean>  {

    const httpParams = new HttpParams();
    httpParams.set('key', ConsentType.tandc);
    httpParams.set('version', '1');



    return this.http.get<{consents: VCommunicationResponse[]}>(`${this.config.get.apiUrl}/members/${dependentID}/communicationpreferences`, {params: httpParams})
      .pipe(
        map(response => {
          return response.consents.filter(consent => consent.key === ConsentType.tandc);
        }),
        map(response => {
          const hasConsent = response.find(res => res.consentStatus === 'Y');
          if (hasConsent) return true;
          return false;
        })
    );
  }

  getFilteredConsent(filter: string, dependentID: string): Observable<VCommunicationResponse>  {
    return this.http.get<{consents: VCommunicationResponse[]}>(`${this.config.get.apiUrl}/members/${dependentID}/communicationpreferences`)
      .pipe(
        map(response => {
          return response.consents.find(consent => consent.key === filter);
        })
    );
  }

  getNotificationConsent(dependentID: string): Observable<VConsentConfig>  {
    return this.http.get<{consents: VCommunicationResponse[]}>(`${this.config.get.apiUrl}/members/${dependentID}/communicationpreferences`)
      .pipe(map(response => {
        return this.convertConsent(response.consents.filter(consent => {
          if (consent.key === ConsentType.research || consent.key === ConsentType.marketing) return consent;
        }));
      }));
  }


}
