import { Component, Input } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';


@Component({
  selector: 'v-error',
  template: `
    <ul class="v-error" *ngIf="control?.touched && control?.dirty && control?.invalid">
      <li>
        <v-icon icon="error"></v-icon>
        <span [translate]="errorMessage"></span>
      </li>
    </ul>
  `,
  styles: [`.v-error{
  color:#d20962;
  list-style:none;
  margin:1.5rem 0;
  padding:0;
  text-align:left; }
`]
})
export class ErrorMessageComponent {

  private static readonly DEFAULT_FIELD: string = 'field';

  @Input('field') control: FormControl;

  get fieldName(): string {
    const formGroup = this.control.parent as FormGroup;

    if (!formGroup) {
      return ErrorMessageComponent.DEFAULT_FIELD;
    }

    return Object
      .keys(formGroup.controls)
      .find(name => formGroup.controls[name] === this.control) || ErrorMessageComponent.DEFAULT_FIELD;
  }

  get errorMessage(): string {
    return `form.error.${this.fieldName}.${Object.keys(this.control.errors)[0] || 'required'}`;
  }

}
