

import { HttpClient } from "@angular/common/http";
import { Injectable } from '@angular/core';
import { Observable } from "rxjs/Observable";
import { of } from "rxjs/observable/of";
import { map } from "rxjs/operators";
import { ConfigService } from "../config/config.service";
import { VActivityResponse } from "../vActivityResponse";
import { VFamilyHistory, VFamilyHistoryResponse } from "./vFamilyHistory";

import { familyMemberConditions } from "../../enums/family-member-conditions";

@Injectable()
export class FamilyHistoryService {

  constructor(private http: HttpClient, private config: ConfigService) {
  }

   convert(response: VFamilyHistoryResponse): VFamilyHistory {

    const familyHistory: VFamilyHistory = {
      familyMemberKey: (response.familyMember && response.familyMember.familyMemberKey) ? response.familyMember.familyMemberKey: '',
      conditionsDescription: (response.conditions && response.conditions.conditionsDescription)?response.conditions.conditionsDescription:'',
      conditionsKey:  (response.conditions && response.conditions.conditionsKey)?response.conditions.conditionsKey:'',
      diagnosisAge: response.diagnosisAge?response.diagnosisAge:'',
      familyHistoryId: response.familyHistoryId?response.familyHistoryId:'',
      familyMemberDescription: (response.familyMember && response.familyMember.familyMemberDescription)?response.familyMember.familyMemberDescription:'',
      individualId: response.individualId?response.individualId:''

    };

    return familyHistory;
  }
  convertAdd(response: VFamilyHistory): VFamilyHistory {

  const familyHistory: VFamilyHistory = {
    familyMemberKey: ( response.familyMemberKey) ? response.familyMemberKey : '',
    conditionsDescription: (response.conditionsDescription) ? response.conditionsDescription : '',
    conditionsKey:  ( response.conditionsKey) ? response.conditionsKey : '',
    diagnosisAge: response.diagnosisAge ? response.diagnosisAge : '',
    familyHistoryId: response.familyHistoryId ? response.familyHistoryId: '',
    familyMemberDescription: ( response.familyMemberDescription) ? response.familyMemberDescription : '',
    individualId: response.individualId ? response.individualId : ''

  };
  return familyHistory;

  }
  convertToKey(description: string): string {
    return familyMemberConditions.filter(res => (res.value).toLowerCase() === description.toLowerCase()).map(res => res.key)[0];
  }

  convertCondition(description: string): string {
    return familyMemberConditions.filter(res => res.key === description).map(res => res.value)[0];
  }

  /**
   * Fetch all family history for an individual
   */
  fetchAll(individualId: string): Observable<VFamilyHistory[]> {
    return this.http.get<{ familyHistory: VFamilyHistoryResponse[] }>(`${this.config.get.apiUrl}/vhealth_members/${individualId}/familyhistories`)
      .pipe(map(response => response.familyHistory.map(familyHistory => this.convert(familyHistory))));
  }

  /**
   * Return a pre-defined list of conditions for use in autofill etc.
   * we only want a string returned to work with what the frontend expects, then map to a key
   * for the save.
   */
  lookup(keyword: string): Observable<string[]> {
    return of(familyMemberConditions.map(res => res.value).filter(res => res.toLowerCase().match(keyword.toLowerCase())));
  }

  /**
   * Add or update a condition.
   */
  save(familyHistory: VFamilyHistory): Observable<VFamilyHistory> {
    // convert description to key
    if (familyHistory.conditionsDescription)
    {
      familyHistory.conditionsKey = this.convertToKey(familyHistory.conditionsDescription);
    }

     const params = {
/*    SEND OVER HARCODED VALUES FOR familyMemberKey and memberConditionKey until this are removed as mandatory  */
      conditionsDescription: familyHistory.conditionsDescription ? familyHistory.conditionsDescription : '',
      conditionsKey: familyHistory.conditionsKey ? familyHistory.conditionsKey : '',
      diagnosisAge: familyHistory.diagnosisAge?familyHistory.diagnosisAge:'',
      familyHistoryId: '',
      familyMemberDescription: familyHistory.familyMemberDescription?familyHistory.familyMemberDescription:'',
      individualId: familyHistory.individualId?familyHistory.individualId:'',
      activity: '',
      familyMemberKey: familyHistory.familyMemberKey?familyHistory.familyMemberKey:"U",
      memberConditionKey: familyHistory.conditionsKey ? familyHistory.conditionsKey : 'AST',
    };

    if (familyHistory.familyHistoryId) {
      params.activity = 'UPDATE';
      params.familyHistoryId = familyHistory.familyHistoryId;
      return this.http.put<VFamilyHistory>(`${this.config.get.apiUrl}/vhealth_familyhistories/${familyHistory.familyHistoryId}`, JSON.stringify(params))
      .pipe(map(response => response.familyHistoryId ? this.convertAdd(response) : familyHistory));
    } else {
      params.activity = 'ADD';
      delete params.familyHistoryId;
      return this.http.post<VFamilyHistory>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/familyhistories`, JSON.stringify(params))
      .pipe(map(response => response.familyHistoryId ? this.convertAdd(response) : familyHistory));
    }
  }

  /**
   * Remove a condition
   */
  remove(familyHistoryId: string): Observable<VActivityResponse> {
    return this.http.delete<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_familyhistories/${familyHistoryId}`);
  }
}
