import { Component, Input, OnChanges } from '@angular/core';
import moment from 'moment-es6';
import { VDependent } from '../../model/vDependent';
import { AppointmentStatus, BookingType } from '../../services/appointments/appointment.service';
import { VAppointmentResponse } from '../../services/appointments/vAppointmentResponse';

@Component({
  selector: 'v-appointment',
  template: `
    <section [class]="'appointment appointment--' + status">
      <span [class]="'appointment__status appointment__status--' + status" [translate]="statusText" *ngIf="!isUpcoming"></span>

      <div class="appointment__subject" [innerHtml]="appointment.subject"></div>

      <v-icon class="appointment__channel" *ngIf="isUpcoming" [tooltip]="appointment.channel" [icon]="appointment.channel"></v-icon>

      <div class="appointment__date">
        <v-icon [icon]="isUpcoming ? 'time' : null"></v-icon>
        <span [innerHtml]="appointment.startTime | moment: 'llll'"></span>
      </div>

      <div class="appointment__user" *ngIf="dependent">
        <v-icon [icon]="isUpcoming ? 'user' : null"></v-icon>
        <span [innerHtml]="dependent.firstName + ' ' + dependent.lastName"></span>
      </div>

      <ng-content></ng-content>
    </section>
  `,
  styles: [`.appointment{
  background:#fff;
  border-top:.7rem solid;
  -webkit-box-shadow:0 2px 3px 0 rgba(65, 65, 65, 0.2);
          box-shadow:0 2px 3px 0 rgba(65, 65, 65, 0.2);
  height:100%;
  padding:0 2rem 1.2rem;
  border-radius:5px;
  text-align:left; }
  @supports (-moz-appearance: none){
    .appointment:hover, .appointment:focus{
      cursor:pointer; } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    .appointment:hover, .appointment:focus{
      cursor:pointer; } }
  @media (hover: hover){
    .appointment:hover, .appointment:focus{
      cursor:pointer; } }
  .appointment--upcoming{
    border-color:#00f0cc;
    padding:2rem; }
  .appointment--follow-up{
    border-color:#d20962; }
  .appointment--pending{
    border-color:#d20962; }
  .appointment--complete{
    border-color:#dcdcdc; }
  .appointment--missed{
    border-color:#646464; }
  .appointment--ready{
    border-color:#7d3f98; }
  @supports (-moz-appearance: none){
    .appointment:hover, .appointment:focus{
      -webkit-box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3);
              box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3); } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    .appointment:hover, .appointment:focus{
      -webkit-box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3);
              box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3); } }
  @media (hover: hover){
    .appointment:hover, .appointment:focus{
      -webkit-box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3);
              box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3); } }

.appointment__status{
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  display:block;
  font:600 1.2rem/2rem "Open Sans";
  padding:1rem 0 .4rem;
  opacity:.7;
  text-transform:uppercase; }
  .appointment__status--upcoming{
    color:#000; }
  .appointment__status--follow-up{
    color:#d20962; }
  .appointment__status--pending{
    color:#d20962; }
  .appointment__status--complete{
    color:#646464; }
  .appointment__status--missed{
    color:#646464; }
  .appointment__status--ready{
    color:#000; }

.appointment__subject{
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  display:block;
  font:600 1.6rem/2.4rem "Open Sans";
  margin:0 0 1rem;
  line-height:1.5;
  font-size:1.6rem; }

.appointment__channel{
  float:right;
  font-size:1.4rem;
  line-height:2;
  margin-left:.5rem; }

.appointment__date{
  color:#646464;
  display:block;
  font:1.2rem/2rem "Open Sans";
  line-height:2.4rem; }
  .appointment__date::before{
    font-size:1.4rem; }

.appointment__user{
  display:block;
  color:#000;
  font:1.2rem/2rem "Open Sans";
  line-height:2.4rem; }
  .appointment__user::before{
    font-size:1.4rem; }

.appointment--ready .appointment__status{
  font:700 1.6rem/2.4rem "Domaine Display";
  margin:2rem 0 1rem;
  opacity:1;
  padding:0;
  text-transform:none;
  white-space:initial; }

.appointment--ready .appointment__subject{
  color:#000;
  font:600 1.2rem/2rem "Open Sans";
  margin:1rem 0; }

.appointment--ready .appointment__date{
  margin:1rem 0 0;
  padding:0; }

.appointment--ready .appointment__user{
  display:none; }
`]
})
export class AppointmentComponent implements OnChanges {

  status: string;

  @Input() appointment: VAppointmentResponse;
  @Input() dependent: VDependent;

  get statusText(): string {
    return `component.appointment.${this.status}`;
  }

  get isUpcoming(): boolean {
    return this.status === 'upcoming';
  }

  ngOnChanges(): void {

    if (!this.appointment) return;

    switch (this.appointment.appointmentStatus) {
      case AppointmentStatus.SCHEDULED:
        if (moment(this.appointment.startTime).isBetween(moment().subtract(3, 'hours'),moment().add(5, 'minutes'))) {
          this.status = 'ready';
        } else {
          this.status = this.appointment.appointmentType === BookingType.FOLLOW_UP ? 'follow-up' : 'upcoming';
        }
        break;
      case AppointmentStatus.COMPLETE:
        this.status = 'complete';
        break;
      case AppointmentStatus.CANCEL_DOCTOR:
      case AppointmentStatus.CANCEL_MEMBER:
        this.status = 'cancelled';
        break;
      case AppointmentStatus.MISSED:
        this.status = 'missed';
        break;
      default:
        this.status = 'pending';
        break;
    }

  }
}
