import { HttpClient } from '@angular/common/http';
import { TranslateLoader } from '@ngx-translate/core';
import { catchError } from 'rxjs/operators';
import { ConfigService } from "../config/config.service";

/**
 * Gets the translations from the server.
 *
 * If the language includes locale (e.g. en-US.json) but there is no locale specific langauge value,
 * fetch the language only variant (en.json) instead.
 */
export class TranslateHttpLoader implements TranslateLoader {

  constructor(private config: ConfigService, private http: HttpClient) {
  }

  private getUrl(lang: string): string {
    return `${this.config.get.translationPrefix}/${lang}.${this.config.get.translationSuffix}`;
  }

  // tslint:disable-next-line
  getTranslation(lang: string): any {
    const url = this.getUrl(lang);

    return this.http.get(url)
      .pipe(catchError(err => {
          if (lang.indexOf('-') !== -1) {
            return this.http.get(this.getUrl(lang.split('-')[0]));
          } else {
            return err;
          }
        })
      );
  }

  static factory(config: ConfigService, http: HttpClient): TranslateHttpLoader {
    return new TranslateHttpLoader(config, http);
  }

}
