import { Component, forwardRef, Input } from "@angular/core";
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";

@Component({
  selector: "v-time-slot",
  template: `
    <button class="time-slot time-slot--{{theme}}"
            type="button"
            [ngClass]="{ 'time-slot--date' : showDate, 'time-slot--selected' : isSelected }"
            (click)="writeValue(date)">
      <span class="time-slot__date" *ngIf="showDate" [innerHtml]="date | dayFormat: {format: 'MMM D', tomorrow: true}"></span>
      <span class="time-slot__time" [innerHtml]="date | moment: 'LT'"></span>
    </button>

  `,
  providers: [
    {provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => TimeSlotComponent), multi: true}
  ],
  styles: [`.time-slot{
  background:#fff;
  border:1px solid #fff;
  border-radius:6px;
  color:#000;
  cursor:pointer;
  text-align:center;
  padding:1.3rem 0;
  white-space:nowrap;
  overflow:hidden;
  width:100%;
  outline:none;
  -webkit-transition:border-color 280ms ease-in-out;
  transition:border-color 280ms ease-in-out; }
  .time-slot.time-slot--date{
    padding:0 0 .2rem; }
  @supports (-moz-appearance: none){
    .time-slot:hover, .time-slot:focus{
      border-color:#d20962; } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    .time-slot:hover, .time-slot:focus{
      border-color:#d20962; } }
  @media (hover: hover){
    .time-slot:hover, .time-slot:focus{
      border-color:#d20962; } }
  .time-slot.time-slot--grey{
    background:#f5f5f5;
    padding:0; }

.time-slot--selected{
  color:#d20962;
  outline:0;
  border-color:#d20962; }

.time-slot__date{
  display:block;
  font:1.6rem/2.4rem "Open Sans";
  font-weight:600;
  margin:.8rem 0 0; }
  .time-slot__date + .time-slot__time{
    margin-bottom:1rem; }

.time-slot__time{
  display:block;
  font:1.6rem/2.4rem "Open Sans";
  font-weight:300; }
`]
})
export class TimeSlotComponent implements ControlValueAccessor {

  private selected: Date;

  @Input() date: Date;
  @Input() theme: string;
  @Input() showDate: boolean;

  // tslint:disable-next-line:no-any
  onChange: (date: Date) => void;
  onTouched: () => void;

  get isSelected(): boolean {
    return this.selected === this.date;
  }

  writeValue(value: Date): void {
    if (this.onChange) {
      this.selected = value;
      this.onChange(value);
    }
  }

  // tslint:disable-next-line:no-any
  registerOnChange(fn: () => {}): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }

}
