import { CommonModule } from '@angular/common';
import { HTTP_INTERCEPTORS, HttpClient, HttpClientModule } from '@angular/common/http';
import { ModuleWithProviders, NgModule } from '@angular/core';
import { TranslateDirective, TranslateLoader, TranslateModule, TranslatePipe } from '@ngx-translate/core';
import { AddThumbnailComponent } from './components/add-thumbnail/add-thumbnail.component';
import { AppointmentSelectComponent } from './components/appointment-select/appointment-select.component';
import { AppointmentComponent } from './components/appointment/appointment.component';
import { DatePickerComponent } from './components/datepicker/datepicker.component';
import { DocumentCardComponent } from './components/document-card/document-card.component';
import { DocumentTypeComponent } from './components/document-type/document-type.component';
import { ErrorMessageComponent } from './components/errorMessage/error-message.component';
import { IconComponent } from './components/icon/icon.component';
import { InputRatingComponent } from './components/input-rating/input-rating.component';
import { LinkCardComponent } from './components/link-card/link-card.component';
import { MembershipCardComponent } from './components/membership-card/membership-card.component';
import { OptionButtonComponent } from './components/option-button/option-button.component';
import { ProgressComponent } from './components/progress/progress.component';
import { RecordGhostComponent } from './components/record-ghost/record-ghost.component';
import { RecordTypeComponent } from './components/record-type/record-type.component';
import { RecordComponent } from './components/record/record.component';
import { TimeSlotComponent } from './components/time-slot/time-slot.component';
import { TruncateComponent } from './components/truncate/truncate.component';
import { DayFormatPipe } from './pipes/day-format.pipe';
import { HighlightPipe } from './pipes/highlight.pipe';
import { MomentPipe } from './pipes/moment.pipe';
import { AllergyService } from './services/allergies/allergy.service';
import { AppointmentService } from './services/appointments/appointment.service';
import { AuthTokenInterceptor } from './services/auth/auth-token.interceptor';
import { AuthService } from './services/auth/auth.service';
import { ConditionService } from './services/conditions/condition.service';
import { Config, CONFIG_TOKEN, ConfigService } from './services/config/config.service';
import { ConsentService } from './services/consent/consent.service';
import { DocumentService } from './services/documents/document.service';
import { FamilyHistoryService } from './services/family-history/family-history.service';
import { FamilyMembersService } from './services/family-members/family-member.service';
import { FAQService } from './services/faqs/faq.service';
import { MedicationService } from './services/medications/medication.service';
import { MemberService } from './services/member/member.service';
import { NotificationService } from './services/notification/notification.service';
import { ShareService } from './services/share/share.service';
import { SurgeryService } from './services/surgeries/surgery.service';
import { TranslateHttpLoader } from './services/translate/translate-http-loader';
import { WellbeingService } from './services/wellbeing/wellbeing.service';
import { DateUtil } from './utils/date.util';
import { StatsUtil } from "./utils/stats.util";

const MODULE_DECLARATIONS = [

  // UI components
  ProgressComponent,
  AppointmentComponent,
  AppointmentSelectComponent,
  TruncateComponent,
  IconComponent,
  DocumentCardComponent,
  ErrorMessageComponent,
  RecordComponent,
  RecordGhostComponent,
  RecordTypeComponent,
  AddThumbnailComponent,
  DocumentTypeComponent,
  MembershipCardComponent,
  LinkCardComponent,

  // Interactive elements
  DatePickerComponent,
  InputRatingComponent,
  OptionButtonComponent,
  TimeSlotComponent,

  // Pipes
  MomentPipe,
  DayFormatPipe,
  HighlightPipe
];

@NgModule({
  declarations: MODULE_DECLARATIONS,
  exports: [
    // Shared from translation module
    TranslatePipe,
    TranslateDirective,

    ...MODULE_DECLARATIONS
  ],
  imports: [
    CommonModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {provide: TranslateLoader, useFactory: TranslateHttpLoader.factory, deps: [ConfigService, HttpClient]},
    })
  ]
})
export class AetnaCommonModule {
  static forRoot(config: Config = {}): ModuleWithProviders {
    return {
      ngModule: AetnaCommonModule,
      providers: [
        AllergyService,
        AppointmentService,
        AuthService,
        CommonModule,
        ConditionService,
        ConsentService,
        DateUtil,
        StatsUtil,
        DocumentService,
        FamilyHistoryService,
        FamilyMembersService,
        FAQService,
        HttpClientModule,
        MedicationService,
        MemberService,
        NotificationService,
        ShareService,
        SurgeryService,
        WellbeingService,
        {provide: HTTP_INTERCEPTORS, useClass: AuthTokenInterceptor, multi: true},
        {provide: CONFIG_TOKEN, useValue: config},
        {provide: ConfigService, useClass: ConfigService, deps: [CONFIG_TOKEN]}
      ]
    };
  }
}
