import { ObjectStore, StoreID } from './object-store.interface';

export class IndexedDBStore implements ObjectStore {

  // This must be incremented every time this file is updated
  private static readonly DATABASE_VERSION: number = 3;
  private static readonly DATABASE_ID: string = 'aetna-vhealth';
  private static db: IDBDatabase = null;

  private userId: string;

  constructor(private storeId: StoreID) {
  }

  static isSupported(): boolean {
    return 'indexedDB' in window;
  }

  /**
   * Single-run promise to fetch the database
   */
  private database: Promise<IDBDatabase> = new Promise<IDBDatabase>((resolve, reject) => {

    if (IndexedDBStore.db) {
      return resolve(IndexedDBStore.db);
    }

    const request = indexedDB.open(IndexedDBStore.DATABASE_ID, IndexedDBStore.DATABASE_VERSION);

    request.onupgradeneeded = () => {
      const database: IDBDatabase = request.result;

      for (const storeId in StoreID) {
        if (!Number(storeId)) {
          const id = StoreID[storeId];

          if (database.objectStoreNames.contains(id)) {
            database.deleteObjectStore(StoreID[storeId]);
          }
          database.createObjectStore(StoreID[storeId], {keyPath: 'id'});
        }
      }

    };

    request.onsuccess = () => {
      IndexedDBStore.db = request.result;
      resolve(IndexedDBStore.db);
    };

    request.onerror = error => reject(error);

  });

  private getIndexKey(key: string): string {
    return btoa(key + this.userId);
  }

  /**
   * Create a new object store transaction
   */
  private getStore(permission: IDBTransactionMode): Promise<IDBObjectStore> {

    if (!this.userId) {
      return Promise.reject('No user id');
    }

    return this.database.then(database => {
      const transaction = database.transaction(this.storeId, permission);
      return transaction.objectStore(this.storeId);
    });
  }

  clear(key?: string): Promise<void> {
    return this.getStore('readwrite').then(store => {
      return new Promise<void>((resolve, reject) => {

        let request: IDBRequest;

        if (key) {
          request = store.delete(this.getIndexKey(key));
        } else {
          request = store.clear();
        }

        request.onsuccess = () => resolve();
        request.onerror = error => reject(error);

      });
    });
  }

  get<T>(key: string): Promise<T> {
    return this.getStore('readonly').then(store => {
      return new Promise<T>((resolve) => {

        const request = store.get(this.getIndexKey(key));
        request.onsuccess = () => resolve(request.result ? request.result.value : null);
        request.onerror = () => resolve(null);

      });
    });

  }

  // tslint:disable-next-line:no-any
  put(key: string, data: any): Promise<void> {
    return this.getStore('readwrite').then(store => {
      return new Promise<void>((resolve, reject) => {

        const id = this.getIndexKey(key);

        const request: IDBRequest = store.put({id, value: data, entry: new Date(), metadata: {key, user: this.userId}});
        request.onsuccess = () => resolve();
        request.onerror = error => reject(error);

      });
    });

  }

  /**
   * Set a user ID.
   */
  set user(userId: string) {
    this.userId = userId;
  }


}
