@Component({
  selector: 'v-record',
  template: `
    <section [class]="'v-record v-record--' + type" [ngClass]="{'v-record--small': !title && !info}" type="button">
      <header class="v-record__header">
        <v-icon class="v-record__icon" [icon]="icon"></v-icon>
      </header>
      <div class="v-record__content">
        <div class="v-record__title" [innerText]="title || ''| titlecase"></div>
        <div class="v-record__tagline" [innerText]="info || '' | titlecase"></div>
      </div>
    </section>
  `,
  styles: [`.v-record{
  background:#fff;
  -webkit-box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.2);
          box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.2);
  border-radius:.6rem;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-orient:vertical;
  -webkit-box-direction:normal;
      -ms-flex-direction:column;
          flex-direction:column;
  font:1.2rem/2rem "Open Sans";
  height:100%;
  overflow:hidden;
  -webkit-transition:-webkit-box-shadow 600ms ease-in-out;
  transition:-webkit-box-shadow 600ms ease-in-out;
  transition:box-shadow 600ms ease-in-out;
  transition:box-shadow 600ms ease-in-out, -webkit-box-shadow 600ms ease-in-out; }
  .v-record .v-record__content{
    -webkit-box-flex:1;
        -ms-flex:1;
            flex:1; }
  .v-record .v-record__tagline{
    font-weight:600; }
  @supports (-moz-appearance: none){
    .v-record:hover, .v-record:focus{
      cursor:pointer;
      -webkit-box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.4);
              box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.4); } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    .v-record:hover, .v-record:focus{
      cursor:pointer;
      -webkit-box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.4);
              box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.4); } }
  @media (hover: hover){
    .v-record:hover, .v-record:focus{
      cursor:pointer;
      -webkit-box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.4);
              box-shadow:0 0.2rem 0.3rem 0 rgba(65, 65, 65, 0.4); } }

.v-record--condition{
  color:#fff;
  min-height:13.5rem; }
  .v-record--condition .v-record__header{
    background:#ef1e7b;
    padding:1rem 1rem 1.4rem 0; }
  .v-record--condition .v-record__content{
    background:#d20962;
    min-height:3.6rem;
    padding:1rem 2rem; }
  .v-record--condition .v-record__icon{
    font-size:4rem;
    line-height:1; }

.v-record--small{
  min-height:initial; }
  .v-record--small .v-record__header{
    padding:.5rem .5rem 1rem; }
  .v-record--small .v-record__icon{
    font-size:1.8rem; }

.v-record--family-history{
  color:#fff;
  min-height:13.5rem; }
  .v-record--family-history .v-record__header{
    background:#66cabb;
    padding:1rem 1rem 1.4rem 0; }
  .v-record--family-history .v-record__content{
    background:#00a78e;
    min-height:3.6rem;
    padding:1rem 2rem; }
  .v-record--family-history .v-record__icon{
    font-size:4rem;
    line-height:1; }

.v-record--small{
  min-height:initial; }
  .v-record--small .v-record__header{
    padding:.5rem .5rem 1rem; }
  .v-record--small .v-record__icon{
    font-size:1.8rem; }

.v-record--surgery{
  border-top:5px solid #7d3f98;
  color:#000;
  padding:1.5rem 2rem;
  min-height:13.5rem; }
  .v-record--surgery.v-record--small{
    min-height:7rem; }
  .v-record--surgery .v-record__icon{
    color:#7d3f98;
    font-size:2rem;
    line-height:2rem; }
    @media (min-width: 800px){
      .v-record--surgery .v-record__icon{
        font-size:2.2rem;
        line-height:2.2rem;
        float:left;
        margin:0 0 1rem; } }
  .v-record--surgery .v-record__header{
    padding-bottom:1rem; }
  .v-record--surgery .v-record__content{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient:vertical;
    -webkit-box-direction:normal;
        -ms-flex-direction:column;
            flex-direction:column;
    -webkit-box-pack:end;
        -ms-flex-pack:end;
            justify-content:flex-end; }

.v-record--medication{
  color:#000;
  padding:1.7rem 2rem; }
  .v-record--medication .v-record__icon{
    color:#d20962;
    font-size:1.4rem;
    line-height:1.4rem; }
    @media (min-width: 800px){
      .v-record--medication .v-record__icon{
        font-size:2rem;
        line-height:2rem;
        float:left;
        margin:0 0 1rem; } }
  .v-record--medication .v-record__title{
    line-height:2.4rem; }
  .v-record--medication .v-record__tagline{
    line-height:1; }

.v-record--allergy{
  color:#000;
  padding:1.7rem 2rem; }
  .v-record--allergy .v-record__icon{
    color:#00a78e;
    font-size:1.4rem;
    line-height:1.4rem; }
    @media (min-width: 800px){
      .v-record--allergy .v-record__icon{
        font-size:2rem;
        line-height:2rem;
        float:left;
        margin:0 0 1rem; } }
  .v-record--allergy .v-record__title{
    line-height:2.4rem; }
  .v-record--allergy .v-record__tagline{
    line-height:1; }
`]
})
export class RecordComponent {

  @Input() title: string;
  @Input() info: string;
  @Input() type: string;

  get icon(): string {

    switch (this.type) {
      case RecordType.CONDITION:
        return 'heart-rate';
      case RecordType.MEDICATION:
        return 'pill';
      case RecordType.SURGERY:
        return 'surgeon';
      case RecordType.ALLERGY:
        return 'allergy';
      case RecordType.HISTORY:
        return 'familyhistoryshort';
      case RecordType.DOCUMENT:
        return 'document';
      default:
        return null;
    }

  }
}
