import { HttpClient } from "@angular/common/http";
import { Injectable } from '@angular/core';
import moment from "moment-es6";
import { Observable } from "rxjs/Observable";
import { of } from "rxjs/observable/of";
import { map } from "rxjs/operators";
import { VDependent } from "../../model/vDependent";
import { DateUtil } from '../../utils/date.util';
import { ConfigService } from "../config/config.service";
import { VActivityResponse } from "../vActivityResponse";
import { medications } from "./medications";
import { VMedication, VMedicationRequest, VMedicationResponse, VMedicationSearchResult } from "./vMedication";

@Injectable()
export class MedicationService {

  frequencies: Array<{key: string, value: string}> = [
    { key: 'BID', value: 'Twice per day'},
    { key: 'TID', value: 'Three times per day'},
    { key: 'QID', value: 'Four times per day'},
    { key: 'FID', value: 'Five times daily'},
    { key: 'WID', value: 'Weekly'},
    { key: 'TWID', value: 'Twice Weekly'},
    { key: 'PRN', value: 'As needed'},
    { key: 'MON', value: 'Monthly'},
    { key: 'OTH', value: 'Other'},
    { key: 'QD', value: 'Once per day'}
  ];

  units: Array<{key: string, value: string}> = [
    { key: 'mg', value: 'mg (Milligram)'},
    { key: 'l', value: 'L (Liters)'},
    { key: 'ml', value: 'ml (Mililiter)'},
    { key: 'meq', value: 'mEq (Milli-equivalent)'},
    { key: 'perc', value: '% (Percent)'},
    { key: 'Gm', value: 'gm (Gram)'},
    { key: 'mcg', value: 'mcg (Microgram)'},
    { key: 'oz', value: 'oz (Ounce)'},
    { key: 'puff', value: 'Puffs'},
    { key: 'Gtts', value: 'gtts (Drops)'},
    { key: 'OTH', value: 'OTH (Other)'}
  ];

  constructor(private http: HttpClient, private config: ConfigService, private dateUtil: DateUtil) {
  }

  private convert(response: VMedicationResponse): VMedication {

    const medication: VMedication = {
      id: response.medicationId,
      name: response.name,
      code: response.code,
      notes: response.notes,
      doctor: response.prescribingDoctor,
      prescription: {
        frequency: response.frequency ? this.convertFrequency(response.frequency) : null,
        dosage: response.dosage && response.dosage !== 'null' ? Number.parseFloat(response.dosage) : null,
        strength: response.dosage && response.dosage !== 'null' ? Number.parseFloat(response.dosage): null,
        unit: response.strengthUnit ? this.convertUnit(response.strengthUnit) : null
      }
    };

    if (response.prescribedFromDate || response.prescribedToDate) {
      medication.duration = {};

      if (response.prescribedFromDate) {
        medication.duration.from = moment(response.prescribedFromDate).toDate();
      }
      if (response.prescribedToDate) {
        medication.duration.to = moment(response.prescribedToDate).toDate();
      }
    }
    return medication;
  }

  convertUnit(unit: string):string {
    const unitConverted = this.units.find(res => res.key === unit);
    return unitConverted ? unitConverted.value : '';
  }

  convertFrequency(frequency: string):string {
    const frequencyConverted = this.frequencies.find(res => res.key === frequency);
    return frequencyConverted ? frequencyConverted.value : '';
  }

  /**
   * Fetch all medications for an individual
   */
  fetchAll(individualId: string): Observable<VMedication[]> {
    return this.http.get<{ medications: VMedicationResponse[] }>(`${this.config.get.apiUrl}/vhealth_members/${individualId}/medications`)
      .pipe(map(response => response.medications.map(medication => this.convert(medication))));
  }

  /**
   * Add or update a medication.
   */
  save(medication: VMedication, dependent: VDependent): Observable<VMedication> {

    const params: VMedicationRequest = {
      activity: 'ADD',
      individualId: dependent.id,
      name: medication.name,
      code: medication.code,
      notes: medication.notes,
      prescribingDoctor: medication.doctor
    };

    if (medication.prescription) {
      params.dosage = String(medication.prescription.dosage);
      params.frequency = medication.prescription.frequency;
      params.strengthUnit = medication.prescription.unit;
    }

    if (medication.duration && medication.duration.from) {
      params.prescribedFromDate = this.dateUtil.formatAPIDate(medication.duration.from);
    }

    if (medication.duration && medication.duration.to) {
      params.prescribedToDate = this.dateUtil.formatAPIDate(medication.duration.to);
    }

    if (medication.id) {
      params.activity = 'UPDATE';
      params.medicationId = medication.id;
      return this.http.put<VMedicationResponse>(`${this.config.get.apiUrl}/vhealth_medications/${medication.id}`, JSON.stringify(params))
      .pipe(map(response => response.medicationId ? this.convert(response) : medication));

    }else{
      return this.http.post<VMedicationResponse>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/medications`, JSON.stringify(params))
      .pipe(map(response => response.medicationId ? this.convert(response) : medication));

    }


  }

  /**
   * Remove a medication
   */
  remove(medicationId: string): Observable<VActivityResponse> {
    return this.http.delete<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_medications/${medicationId}`);
  }

  /**
   * Search the array for medication details
   */
  search(query: string): Observable<VMedicationSearchResult[]> {
    let medicationResults: VMedicationSearchResult[] = [];

    if(query !== undefined) {
      query = query.toLowerCase();

      medicationResults = medications.filter(medication => {
        return medication.toLowerCase().includes(query);
      }).map(medication => {
        return {
          medicationName: medication,
          medicationCode: medication
        };
      });
    }
    return of(medicationResults);
  }
}
