import { Pipe, PipeTransform } from '@angular/core';
import moment from 'moment-es6';

@Pipe({name: 'dayFormat'})
export class DayFormatPipe implements PipeTransform {

  transform(date: Date | moment.Moment | string | number, opts: FormatOptions = {}): string {

    if (!date) return '';
    if (!opts.format) opts.format = 'D MMM';

    return moment(date).calendar(null, {
      lastWeek: opts.format,
      lastDay: opts.format,
      sameDay: '[Today]',
      nextDay: opts.tomorrow ? '[Tomorrow]' : opts.format,
      nextWeek: opts.format,
      sameElse: opts.format
    });

  }

}

export interface FormatOptions {

  /**
   * Set the default format if words not used.
   */
  format?: string;

  /**
   * Set whether or not tomorrow is formatted
   * as a standard date or word.
   */
  tomorrow?: boolean;

}
