

import { HttpClient } from "@angular/common/http";
import { Injectable } from '@angular/core';
import { Observable } from "rxjs/Observable";
import { map } from "rxjs/operators";
import { ConfigService } from "../config/config.service";
import { VFamilyMember } from "./vFamilyMember";


@Injectable()
export class FamilyMembersService {

  constructor(private http: HttpClient, private config: ConfigService) {
  }

  /**
   * Fetch family members
   */
  fetch(id: string): Observable<VFamilyMember[]> {
    return this.http.get<{ familyMembers: VFamilyMember[] }>(`${this.config.get.apiUrl}/vhealth_members/${id}/family`)
    .pipe(map(response => response.familyMembers));
  }
}
