import { Component, forwardRef, Input } from "@angular/core";
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";

@Component({
  selector: "v-input-rating",
  template: `
    <section>
      <button type="button" [attr.disabled]="readonly || disabled" (click)="onClick(rating)" [attr.selected]="rating <= value" [attr.value]="rating" *ngFor="let rating of ratings" [ngClass]="{large: isLarge, web: isWeb}">
        <svg viewBox="0 0 14 14" version="1.1" xmlns="http://www.w3.org/2000/svg">
          <path d="M13.9858268,5.04711139 C13.9450547,4.9213744 13.828188,4.83572747 13.695486,4.83572747 L8.74508376,4.83572747 L7.29032675,0.189523735 C7.21058004,-0.0631745785 6.78876413,-0.0631745785 6.70904595,0.189523735 L5.25428894,4.83572747 L0.304514378,4.83572747 C0.171812418,4.83572747 0.054945673,4.9213744 0.0141735592,5.04711139 C-0.025400214,5.17284838 0.0196516878,5.31011695 0.127388218,5.38727891 L4.19823698,8.28834097 L2.45436602,12.9296758 C2.40748808,13.0548149 2.44766102,13.195728 2.55357151,13.2771324 C2.66008117,13.3591348 2.80556543,13.3603592 2.91635487,13.2838236 L6.99998593,10.3724258 L11.083617,13.2838236 C11.1371714,13.3214935 11.1986577,13.3403141 11.2607432,13.3403141 C11.3258815,13.3403141 11.3916191,13.3196712 11.4464004,13.2771324 C11.5523108,13.195728 11.5924838,13.0548149 11.5456058,12.9296758 L9.80173489,8.28834097 L13.8725837,5.38727891 C13.9803487,5.31011695 14.0254006,5.17284838 13.9858268,5.04711139 Z" id="path-1"></path>
        </svg>
      </button>
    </section>
  `,
  providers: [
    {provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => InputRatingComponent), multi: true}
  ],
  styles: [`section{
  display:-webkit-inline-box;
  display:-ms-inline-flexbox;
  display:inline-flex;
  -webkit-box-orient:horizontal;
  -webkit-box-direction:reverse;
      -ms-flex-direction:row-reverse;
          flex-direction:row-reverse; }

button{
  background:none;
  margin-right:4px;
  padding:0; }
  button svg{
    fill:#dcdcdc;
    height:1.4rem;
    width:1.4rem; }
  button.large svg{
    height:4.5rem;
    width:4.5rem; }
  button.web{
    margin-right:7px; }
    button.web svg{
      height:2.4rem;
      width:2.4rem; }
    button.web:not([disabled]):hover svg,
    button.web:not([disabled]):hover ~ button:not([selected='true']) svg{
      fill:#00a78e; }
    button.web[selected='true'] svg{
      fill:#00f0cc; }
  button:not([disabled]):hover svg,
  button:not([disabled]):hover ~ button:not([selected='true']) svg{
    fill:#e46b95; }
  button[selected='true'] svg{
    fill:#d20962; }
`]
})
export class InputRatingComponent implements ControlValueAccessor {

  @Input() step: number = 1;
  @Input() min: number = 1;
  @Input() max: number = 5;

  @Input() isLarge: boolean;
  @Input() isWeb: boolean;
  @Input() disabled: boolean;
  @Input() readonly: boolean;

  value: number;
  onChange: (value: number) => void;
  onTouched: () => void;

  get ratings(): number[] {
    const array = [];

    for (let i = this.max; i >= this.min; i = i - this.step) {
      array.push(i);
    }

    return array;
  }

  onClick(value: number): void {
    if (this.readonly || this.disabled) return;
    this.writeValue(value);
  }

  writeValue(value: number): void {
    if (this.onChange) {
      this.onChange(value);
      this.value = value;
    }
  }

  registerOnChange(fn: () => {}): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }

}
