import { Component, forwardRef, Input } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";
import { VAppointmentResponse } from "../../services/appointments/vAppointmentResponse";

@Component({
  selector: 'v-appointment-select',
  template: `
    <button class="v-appointment-select"
            type="button"
            (click)="writeValue(appointment)"
            [ngClass]="{'v-appointment-select--selected': isSelected}">
      <span class="v-appointment-select__subject" [innerHtml]="appointment.subject"></span>
      <span class="v-appointment-select__date" [innerHtml]="appointment.startTime | moment"></span>
    </button>
  `,
  providers: [
    {provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => AppointmentSelectComponent), multi: true}
  ],
  styles: [`.v-appointment-select{
  background:#fff;
  border-radius:6px;
  border:1px solid #fff;
  cursor:pointer;
  display:block;
  padding:1rem 2rem 1.2rem;
  outline:none;
  text-align:left;
  -webkit-transition:border-color 280ms;
  transition:border-color 280ms;
  width:100%; }
  @supports (-moz-appearance: none){
    .v-appointment-select:hover, .v-appointment-select:focus{
      border-color:#b18cc1; } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    .v-appointment-select:hover, .v-appointment-select:focus{
      border-color:#b18cc1; } }
  @media (hover: hover){
    .v-appointment-select:hover, .v-appointment-select:focus{
      border-color:#b18cc1; } }
  .v-appointment-select--selected{
    border-color:#b18cc1; }

.v-appointment-select__subject{
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  color:#7d3f98;
  display:block;
  font:1.6rem/2.4rem "Open Sans";
  font-weight:600;
  line-height:2.4rem; }

.v-appointment-select__date{
  color:#646464;
  display:block;
  font:1.6rem/2.4rem "Open Sans";
  line-height:2rem; }
`]
})
export class AppointmentSelectComponent implements ControlValueAccessor {

  private selected: VAppointmentResponse;

  @Input() appointment: VAppointmentResponse;

  // tslint:disable-next-line:no-any
  onChange: (appointment: VAppointmentResponse) => void;
  onTouched: () => void;

  get isSelected(): boolean {
    return this.selected === this.appointment;
  }

  writeValue(value: VAppointmentResponse): void {
    if (this.onChange) {
      this.selected = value;
      this.onChange(value);
    }
  }

  // tslint:disable-next-line:no-any
  registerOnChange(fn: () => {}): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }

}
