import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'highlight'})
export class HighlightPipe implements PipeTransform {

  transform(content: string, match: string, opts: HighlightOptions = {}): string {

    if (!content) return '';

    const pattern = new RegExp(`(${match})`, opts.first ? 'i' : 'gi');

    return content.replace(pattern, match => `<span class="${opts.toggleClass || 'v-highlight'}">${match}</span>`);
  }

}

export interface HighlightOptions {

  /**
   * Specify the class to be added to the highlighted option.
   * Defaults to 'v-highlight'.
   */
  toggleClass?: string;

  /**
   * If true, match only the first result.
   */
  first?: boolean;

}
