import { HttpClient } from "@angular/common/http";
import { Injectable } from '@angular/core';
import moment from 'moment-es6';
import { Observable } from "rxjs/Observable";
import { map } from "rxjs/operators";
import { VDependent } from "../../model/vDependent";
import { ConfigService } from "../config/config.service";
import { VActivityResponse } from "../vActivityResponse";
import { VSurgery, VSurgeryRequest, VSurgeryResponse } from "./vSurgery";

@Injectable()
export class SurgeryService {

  constructor(private http: HttpClient, private config: ConfigService) {
  }

  private convert(response: VSurgeryResponse): VSurgery {

    const surgery: VSurgery = {
      id: response.surgeryId,
      name: response.procedureDescription,
      code: response.procedureCode,
      notes: response.notes,
      doctor: response.doctor,
      hospital: response.hospital,
      date: response.date
    };

    return surgery;
  }

  /**
   * Fetch all surgeries for an individual
   */
  fetchAll(individualId: string): Observable<VSurgery[]> {
    return this.http.get<{ surgeries: VSurgeryResponse[] }>(`${this.config.get.apiUrl}/vhealth_members/${individualId}/surgeries`)
      .pipe(map(response => response.surgeries.map(surgery => this.convert(surgery))));
  }

  /**
   * Add or update a surgery.
   */
  save(surgery: VSurgery, dependent: VDependent): Observable<VSurgery> {

    const params: VSurgeryRequest = {
      activity: 'ADD',
      individualId: dependent.id,
      procedureDescription: surgery.name,
      procedureCode: surgery.code,
      notes: surgery.notes,
      doctor: surgery.doctor,
      hospital: surgery.hospital
    };

    if (surgery.date) params.date = moment(surgery.date).format('YYYY-MM-DD HH:mm');

    if (surgery.id) {
      params.activity = 'UPDATE';
      params.surgeryId = surgery.id;
      return this.http.put<VSurgeryResponse>(`${this.config.get.apiUrl}/vhealth_surgeries/${params.surgeryId}`, JSON.stringify(params))
      .pipe(map(response => response.surgeryId ? this.convert(response) : surgery));

    } else {
      return this.http.post<VSurgeryResponse>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/surgeries`, JSON.stringify(params))
      .pipe(map(response => response.surgeryId ? this.convert(response) : surgery));
    }


  }

  /**
   * Remove a surgery
   */
  remove(surgeryId: string): Observable<VActivityResponse> {

    return this.http.delete<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_surgeries/${surgeryId}`);

  }

}
