
export class ValidationPattern {

  /**
   * Email validation pattern.
   *
   * @example
   * VALID: a@b.co
   * INVALID: a
   * INVALID: a@b
   * INVALID: a@b.c
   * INVALID: a.com
   */
  static readonly EMAIL: string = '^[a-z0-9._%+-]+@[a-z0-9.-]+\\.[a-z]{2,4}$';

  /**
   * Phone number validation pattern.
   * Every character except numbers are valid.
   */
  static readonly PHONE: string = '^([^a-zA-Z]*)$';

}
