import { HttpErrorResponse, HttpEvent, HttpHandler, HttpInterceptor, HttpRequest } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { _throw as throwError } from 'rxjs/observable/throw';
import { catchError, flatMap } from 'rxjs/operators';
import { ConfigService } from '../config/config.service';
import { AuthService, AuthStatus } from './auth.service';

@Injectable()
export class AuthTokenInterceptor implements HttpInterceptor {

  constructor(private config: ConfigService, private userService: AuthService) {
  }

  /**
   * Checks if the URL requires an attached authentication token.
   *
   * @param url
   * @returns true if authentication headers required
   */
  private checkUrlRequiresAuth(url: string): boolean {

    // First check if the URL is a proper API URL
    if (url.startsWith(this.config.get.apiUrl)) {

      // Filter whitelisted URL
      const iswhitelist = [
        this.config.get.inviteUrl,
        '/faqs',
        '/vhealth_faqs'
      ].find(whitelist => {
        const match = url.match(whitelist);
        return match && match.length > 0;
      });

      if (!iswhitelist) {
        return true;
      }
    }
    if (url.startsWith(this.config.get.inviteUrl)){
      return true;
    }
    return false;
  }

  /**
   * The authentication token interceptor checks each HTTP request which contacts the APIc. *
   * For (almost) all URLs we need to attach the {@link Auth auth token} in the request headers.
   *
   * If authentication is required, then we pass through the auth service, checking if the current authentication is still valid.
   * If it is, then the relevant headers are attached to each request. If it is not, or an error occurs during the API request, then the service
   * publishes a log out event: ({@link AuthStatus.INACTIVE} if the API returns error, or {@link AuthStatus.LOGGED_OUT} if the authentication has expired.
   */
  // tslint:disable-next-line:no-any
  intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {

    const authRequired = this.checkUrlRequiresAuth(request.url);

    // Only authenticate API requests
    if (!authRequired) {
      return next.handle(request);
    }

    // Check if user is authenticated and add authentication headers
    return this.userService.isLoggedIn().pipe(flatMap(token => {

      if (token) {
        request = request.clone({
          setHeaders: {
            'Authorization': token.accessToken,
            'X-IBM-Client-Id': token.clientId,
            'ID_Token': token.idToken,
          }
        });

        return next.handle(request).pipe(catchError(error => {

          // Log out when API says so
          if (error instanceof HttpErrorResponse && error.status === 401) {
            this.userService.changeStatus(AuthStatus.INACTIVE);
          }

          return throwError(error);

        }));

      } else {
        return throwError(AuthStatus.LOGGED_OUT);
      }

    }));

  }
}
