import { Component, Input, OnChanges } from '@angular/core';

@Component({
  selector: 'v-truncate',
  styles: [`:root{
  display:inline; }

button{
  background:none;
  border:0;
  color:#7d3f98;
  cursor:pointer;
  font-family:"Open Sans";
  font-size:inherit;
  font-weight:600;
  outline:0;
  padding:0; }
  @supports (-moz-appearance: none){
    button:hover, button:focus{
      color:#b18cc1; } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    button:hover, button:focus{
      color:#b18cc1; } }
  @media (hover: hover){
    button:hover, button:focus{
      color:#b18cc1; } }
`],
  template: `
    {{concat && !showAll ? concat : content}}    
    <button *ngIf="concat" (click)="showAll = !showAll" [translate]="buttonText"></button>
  `,
})
export class TruncateComponent implements OnChanges {

  concat: string;
  showAll: boolean;

  @Input() length: number = 60;
  @Input() content: string;

  get buttonText(): string {
    return `component.truncate.${this.showAll ? 'less' : 'more'}`;
  }

  ngOnChanges(): void {
    delete this.concat;

    if (this.content && this.content.length > this.length) {
      this.concat = this.content.slice(0, this.length) + '...';
    }
  }


}
