import { HttpClient } from "@angular/common/http";
import { Injectable } from '@angular/core';
import { TranslateService } from "@ngx-translate/core";

import { Observable } from "rxjs/Observable";
import { of } from "rxjs/observable/of";
import { map } from "rxjs/operators";
import { VDependent } from "../../model/vDependent";
import { ConfigService } from "../config/config.service";
import { VActivityResponse } from "../vActivityResponse";
import { conditions } from "./conditions";
import { VCondition } from "./vCondition";
export enum ConditionStatus {
  PAST = 'PT',
  CURRENT = 'CT',
  DT = 'DT',
  FR = 'FR',
  PR = 'PR',
  ST = 'ST',
  UST = 'UST',
  CT = 'CT',
  PT = 'PT',
  FAMILY = 'family-history'
}

const currentConditionStatus: [string] = ['DT', 'PR', 'ST', 'UST', 'CT'];
const pastConditionStatus: [string] = ['PT', 'FR'];

@Injectable()
export class ConditionService {

  constructor(private http: HttpClient, private config: ConfigService,
    private translate: TranslateService) {
      this.translate.setDefaultLang('en');
  }

  /**
   * Fetch all conditions for an individual
   */
  fetchAll(individualId: string): Observable<VCondition[]> {
    return this.http.get<{ conditions: VCondition[] }>(`${this.config.get.apiUrl}/vhealth_members/${individualId}/conditions`)
      .pipe(map(response => {
        return response.conditions.map(res => {
          const conditions = res;
          conditions.status = this.convertStatus(res.status);
          conditions.statusDescription = this.convertStatusDescription(res.status);
          return res;
        });
      }));
  }

  convertStatus(status: ConditionStatus):ConditionStatus {
    if (currentConditionStatus.includes(status)) return ConditionStatus.CURRENT;
    else if (pastConditionStatus.includes(status)) return ConditionStatus.PAST;
    return null;
  }
  convertStatusDescription(status: ConditionStatus):string {
    let translateStr = '';
    if (currentConditionStatus.includes(status)) translateStr = 'component.record-type.condition.status.current';
    else if (pastConditionStatus.includes(status)) translateStr = 'component.record-type.condition.status.past';
    else return null;

    return this.translate.instant(translateStr);
  }

  /**
   * Add or update a condition.
   */
  save(condition: VCondition, dependent: VDependent): Observable<VCondition> {

    const params = {
      individualId: dependent.id,
      conditionName: condition.conditionName,
      conditionCode: condition.conditionCode,
      status: condition.status,
      statusDescription: condition.statusDescription,
      conditionId: '',
      activity: ''
    };

    if (condition.conditionId) {
      params.activity = 'UPDATE';
      params.conditionId = condition.conditionId;
      return this.http.put<VCondition>(`${this.config.get.apiUrl}/vhealth_conditions/${params.conditionId}`, JSON.stringify(params))
      .pipe(map(response => response.conditionId ? response : condition));
    } else {
      params.activity = 'ADD';
      delete params.conditionId;
      return this.http.post<VCondition>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/conditions`, JSON.stringify(params))
      .pipe(map(response => response.conditionId ? response : condition));
    }
  }

  /**
   * Remove a condition
   */
  remove(conditionId: string): Observable<VActivityResponse> {

    return this.http.delete<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_conditions/${conditionId}`);

  }

  /**
   * Return a pre-defined list of conditions for use in autofill etc.
   */
  lookup(keyword : string): Observable<string[]> {
    return of(conditions.filter(res=> res.toLowerCase().match(keyword.toLowerCase())));
  }

  /**
   * Search the API for conditions
   */
  search(keyword : string): Observable<VCondition[]> {

    return this.http.get<{ conditions : VCondition[] }>(`${this.config.get.apiUrl}/v3/vhealth_conditions`)
      .pipe(map(response => response.conditions.filter(res => res.conditionName.toLowerCase().match(keyword.toLowerCase()))));

  }


}
