import { Component, Input } from '@angular/core';
import { FileType } from '../../enums/file-type';
import { VDocument } from '../../services/documents/vDocumentResponse';

@Component({
  selector: 'v-document-card',
  template: `
    <article class="v-document-card">
      <v-icon class="v-document-card__icon" [icon]="icon"></v-icon>
      <div class="v-document-card__title" [innerText]="document.title"></div>
      <div class="v-document-card__date" [innerText]="document.created | moment"></div>
    </article>
  `,
  styles: [`.v-document-card{
  background:#fff;
  border-radius:6px;
  -webkit-box-shadow:0 2px 3px rgba(65, 65, 65, 0.2);
          box-shadow:0 2px 3px rgba(65, 65, 65, 0.2);
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-align:center;
      -ms-flex-align:center;
          align-items:center;
  font:1.2rem/2rem "Open Sans";
  height:5.5rem;
  padding:0 2rem; }

.v-document-card__icon{
  color:#7d3f98;
  font-size:1.4rem;
  line-height:1; }

.v-document-card__title{
  overflow:hidden;
  text-overflow:ellipsis;
  white-space:nowrap;
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  margin:0 1rem; }

.v-document-card__date{
  color:#646464;
  padding-left:.8rem; }
`]
})
export class DocumentCardComponent {

  @Input() document: VDocument;

  get icon(): string {

    if (!this.document) return;

    switch (this.document.type) {
      case FileType.IMAGE:
        return 'camera';
      default:
        return 'document';
    }

  }
}
