import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { utc } from 'moment';
import moment from 'moment-es6';
import { Observable } from 'rxjs/Observable';
import { map } from 'rxjs/operators';
import { VAppointmentAvailability } from '../../model/vAppointmentAvailability';
import { VDependent } from '../../model/vDependent';
import { DateUtil } from '../../utils/date.util';
import { StatsUtil } from "../../utils/stats.util";
import { ConfigService } from '../config/config.service';
import { VActivityResponse } from '../vActivityResponse';
import { VAppointmentAvailabilityRequest } from './vAppointmentAvailabilityRequest';
import { VAppointmentAvailabilityResponse } from './vAppointmentAvailabilityResponse';
import { VAppointmentBookingRequest } from './vAppointmentBookingRequest';
import { VAppointmentRequest } from './vAppointmentRequest';
import { VAppointmentLinks, VAppointmentResponse } from './vAppointmentResponse';


export enum BookingAction {
  BOOK = 'BOOK',
  CANCEL = 'CANCEL'
}

export enum BookingChannel {
  VIDEO = 'Video',
  PHONE = 'Phone'
}


export enum BookingType {
  INITIAL = 'Initial',
  NEW = 'Consultation',
  FOLLOW_UP = 'Follow-Up'
}

export enum AppointmentStatus {
  SCHEDULED = 'Appointment_Scheduled',
  CANCEL_MEMBER = 'Appointment_Canceled_Member_Not_Verified',
  CANCEL_DOCTOR = 'Appointment_Canceled',
  MISSED = 'Appointment_Missed',
  DOCTOR_WAITING = 'Appointment_Doctor_Ready_for_Patient',
  PATIENT_WAITING = 'Appointment_Patient_in_Waiting_Room',
  IN_PROGRESS = 'Appointment_In_Progress',
  CALL_ENDED = 'Appointment_Call_ended',
  COMPLETE = 'Appointment_Completed'
}

@Injectable()
export class AppointmentService {

  constructor(private http: HttpClient, private config: ConfigService, private dateUtil: DateUtil, private statsUtil: StatsUtil) {
  }

  private convertAppointment(appointment: VAppointmentResponse): VAppointmentResponse {

    appointment.appointmentStatus = appointment.appointmentstatus || appointment.appointmentStatus;

    if (appointment.startTime) appointment.startTime = this.dateUtil.convertAPIDateToString(appointment.startTime);
    if (appointment.appointmentType) {
      switch (appointment.appointmentType.substr(0, appointment.appointmentType.indexOf(' '))) {
        case 'Follow-Up':
          appointment.appointmentType = BookingType.FOLLOW_UP;
          break;
        case 'Consultation':
          appointment.appointmentType = BookingType.NEW;
          break;
        case 'Initial':
          appointment.appointmentType = BookingType.INITIAL;
          break;
        default:
          break;
      }
    }
    return appointment;
  }

  /**
   * Retrieve appointment links
   */
  getAppointmentLinks(appointment: VAppointmentResponse, dependent: VDependent, osFromApp: string = null): Observable<VAppointmentLinks> {

    const info = this.statsUtil.getDeviceInfo(osFromApp);

    const params = {
      memberId: appointment.appointmentId,
      apptDetails: appointment.subject,
      memberName: dependent.firstName,
      os: info['os'],
      isMobile: info['isMobile'],
      browser: info['browser']
    };

    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };

    return this.http.post<VAppointmentLinks>(`${this.config.get.consultationUrl}`, JSON.stringify(params), httpOptions);

  }

  /**
   * Fetch multiple appointments.
   */
  getAppointments(params: VAppointmentRequest): Observable<VAppointmentResponse[]> {

    const from = params.from ? this.dateUtil.formatAPIDate(params.from) : null;
    const to = params.to ? this.dateUtil.formatAPIDate(params.to) : null;
    const cancelledStatuses = [AppointmentStatus.CANCEL_MEMBER, AppointmentStatus.CANCEL_DOCTOR];

    const httpParams = new HttpParams();
    if (from) {
      httpParams.set('from', from);
    }
    if (to) {
      httpParams.set('to', to);
    }

    return this.http.get<{ appointments: VAppointmentResponse[] }>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/appointments`, { params: httpParams })
      .pipe(map(res => res.appointments
        .map(appointment => this.convertAppointment(appointment))
        .filter(appointment => cancelledStatuses.indexOf(appointment.appointmentStatus) === -1))
      );
  }

  /**
   * Fetch single appointment.
   */
  getAppointment(appointmentId: string): Observable<VAppointmentResponse> {

    return this.http.get<VAppointmentResponse>(`${this.config.get.apiUrl}/vhealth_appointments/${appointmentId}`)
      .pipe(map(appointment => this.convertAppointment(appointment)));

  }

  /**
   * To provide provision to user to book appointment.
   * For all the request device Id and bearer token is required to identify the user.
   */
  bookAppointment(params: VAppointmentBookingRequest): Observable<VAppointmentResponse> {
    let secondaryPhoneConcat: string;
    // We only add number to doctor notes if it's the secondary number the one the user chose
    if (params.contactNumber && params.contactNumber.code && params.contactNumber.number && !params.isPrimary) {
      secondaryPhoneConcat = '*** Secondary Phone Number ' + params.contactNumber.code + ' ' + params.contactNumber.number + ' ***';
    } else {
      secondaryPhoneConcat = '';
    }
    const request = {
      actionType: BookingAction.BOOK,
      appointmentType: params.bookingType !== undefined ? params.bookingType.toString() : BookingType.NEW,
      channel: params.bookingChannel.toString(),
      reason: params.reason,
      individualId: String(params.individualId),
      startDate: this.dateUtil.formatAPIDate(params.date),
      subject: params.subject,
      doctorNotes: secondaryPhoneConcat
    };

    return this.http.post<{ appointment: VAppointmentResponse }>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/appointments`, JSON.stringify(request))
      .pipe(map(appointment => this.convertAppointment(appointment.appointment)));
  }

  /**
   * To provide provision to user to cancel appointment.
   * For all the request device Id and bearer token is required to identify the user.
   */
  cancelAppointment(appointmentId: string): Observable<VActivityResponse> {

    const id = String(appointmentId);

    return this.http.delete<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_appointments/${id}`);
  }

  /**
   * To provide provision to user to rate the appointment.
   * For all the request device Id and bearer token is required to identify the user.
   */
  rateAppointment(appointmentId: string, rating: number, feedback?: string): Observable<VActivityResponse> {

    const request = {
      rating: String(Math.min(Math.max(Math.round(rating), 1), 5)),
      feedback
    };
    return this.http.put<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_appointments/${appointmentId}/appointmentrating`, JSON.stringify(request));
  }

  /**
   * Get appointment availability
   */
  getAppointmentAvailability(params: VAppointmentAvailabilityRequest, appointments: VAppointmentResponse[] = []): Observable<VAppointmentAvailability[]> {
    //transform dates
    const dateFromTransformed = this.dateUtil.formatAPIDate(this.dateUtil.roundTime(new Date(params.dateFrom)));
    const dateToTransformed = this.dateUtil.formatAPIDate(this.dateUtil.roundTime(new Date(params.dateTo)));
    const httpParams: HttpParams = new HttpParams()
      .append('from', dateFromTransformed)
      .append('to', dateToTransformed)
      .append('appointmentType', appointments.length>0 ? BookingType.NEW : BookingType.INITIAL);
         
    return this.http.get<VAppointmentAvailabilityResponse>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/appointmentslots`, {params: httpParams})
      .pipe(map(res => {
        const filteredAppointments = new Map<string, VAppointmentAvailability>();

        const appointmentsUTC = appointments.filter(result => {
          result.startTimeM = utc(new Date(result.startTime)).subtract(1,'minute');

          if(result.appointmentType === BookingType.INITIAL) {
            result.endTimeM = utc(new Date(result.startTime)).add(1,'hour');
          } else {
            result.endTimeM = utc(new Date(result.startTime)).add(30,'minutes');
          }
          return res;
        });

        const timeSlotsUTC = res.availability.filter(ts => {
          ts.startTimeD = this.dateUtil.convertAPIDate(ts.startTime);
          ts.startTimeM = moment(ts.startTimeD);
          ts.endTimeD = this.dateUtil.convertAPIDate(ts.endTime);
          return ts;
        });

        return timeSlotsUTC
          .filter(timeSlot => !appointmentsUTC.find(appointment => timeSlot.startTimeM.isBetween(appointment.startTimeM, appointment.endTimeM)))
          .map(appointment => {
            return {
              startTime: appointment.startTimeD,
              endTime: appointment.endTimeD,
              type: appointment.appointmentType,
              doctors: [appointment.doctor.id]
            };
          })
          .filter(timeSlot => {
            const id = timeSlot.type + timeSlot.startTime.toString();
            const prev = filteredAppointments.get(id);

            if (prev) {
              prev.doctors.push(timeSlot.doctors[0]);
              return false;
            }
            filteredAppointments.set(id, timeSlot);
            return true;
          });

      }));
  }
}
