import { Component, Input } from "@angular/core";

@Component({
  selector: "v-icon",
  template: `
    <i class="v-icon v-icon--{{icon}}" [attr.v-icon]="icon" [ngClass]="{'v-icon--mobile': isMobileOnly}" [attr.role]="tooltip ? 'presentation': 'img'" [attr.aria-hidden]="!tooltip">
      <span class="v-icon__tooltip" *ngIf="tooltip" [innerHtml]="tooltip"></span>
    </i>
  `,
  styles: [`:host{
  font-family:"vHealth Icon Font"; }
  :host:only-child{
    vertical-align:middle; }

.v-icon{
  font:inherit; }

.v-icon__tooltip{
  position:absolute;
  height:1px;
  width:1px;
  overflow:hidden;
  clip:rect(1px, 1px, 1px, 1px); }

@media (min-width: 800px){
  .v-icon--mobile{
    display:none; } }
`]
})
export class IconComponent {
  @Input() tooltip: string;
  @Input() isMobileOnly: boolean;
  @Input() icon: string;
}
