import { ObjectStore, StoreID } from './object-store.interface';

export class LocalStorageStore implements ObjectStore {

  private userId: string;

  constructor(private storeId: StoreID) {
  }

  buildKey(key: string) : string {
    return btoa(`${this.storeId}-${key}-${this.userId}`);
  }

  /**
   * Clear one or all values in storage
   */
  clear(key?: string): Promise<void> {

    if (!this.userId) {
      return Promise.reject('No user ID provided');
    }

    if (key) {
      localStorage.removeItem(this.buildKey(key));
    } else {
      localStorage.clear();
    }

    return Promise.resolve();
  }

  /**
   * Retrieve an item from storage
   */
  get<T>(key: string): Promise<T> {

    if (!this.userId) {
      return Promise.reject('No user ID provided');
    }

    const storage = localStorage.getItem(this.buildKey(key));
    return Promise.resolve(storage ? JSON.parse(storage).data : null);

  }

  /**
   * Save an item into storage
   */
  // tslint:disable-next-line:no-any
  put(key: string, data: any): Promise<void> {

    if (!this.userId) {
      return Promise.reject('No user ID provided');
    }

    const storageItem = {
      date: Date.now(),
      data
    };

    localStorage.setItem(this.buildKey(key), JSON.stringify(storageItem));
    return Promise.resolve();

  }

  /**
   * Set a user ID.
   */
  set user(userId: string) {
    this.userId = userId;
  }

}
