import { Injectable } from '@angular/core';

export interface DeviceInfo {
  // tslint:disable-next-line:no-any
  [key:string]: any; // missing index definition
  browser     : string;
  os          : string;
  isMobile    : boolean;
}

declare var navigator: BrowserNavigator;
declare var document: BrowserDocument;

interface BrowserDocument extends Document {
  documentMode: string;
}

interface BrowserNavigator extends Navigator {
  oscpu: string;
  platform: string;
  userAgent: string;
}

@Injectable()
export class StatsUtil {

  constructor () {
  }

  /**
   * Get Device Info (browser, os, is on Mobile Device)
   * Can be
   *
   * @param osFromApp : OS if sending from app, null otherwise
   */
  getDeviceInfo (osFromApp: string): DeviceInfo {

    let browser, mobile, os;

    if (!osFromApp) {

      // Getting main Browsers with Javascript
      // https://stackoverflow.com/questions/9847580/how-to-detect-safari-chrome-ie-firefox-and-opera-browser
      if((navigator.userAgent.indexOf("Opera") || navigator.userAgent.indexOf('OPR')) !== -1 )
      {
        browser = ('Opera');
      }
      else if(navigator.userAgent.indexOf("Chrome") !== -1 )
      {
        browser = ('Chrome');
      }
      else if(navigator.userAgent.indexOf("Safari") !== -1)
      {
        browser = ('Safari');
      }
      else if(navigator.userAgent.indexOf("Firefox") !== -1 )
      {
        browser = ('Firefox');
      }
      else if((navigator.userAgent.indexOf("MSIE") !== -1 ) || (!!document['documentMode'] === true )) //IF IE > 10
      {
        browser = ('IE');
      }
      else
      {
        browser = ('unknown');
      }

      // Getting the os
      os        = 'unknown';
      if (navigator['oscpu']) {
        os = navigator['oscpu'];
      } else if (navigator['platform']) { // Chrome
        os = navigator['platform'];
      }

      // Get if from mobile
      mobile = false;
      // device detection (Javascript)
      // https://stackoverflow.com/questions/3514784/what-is-the-best-way-to-detect-a-mobile-device-in-jquery
      if( /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent) ) {
        mobile = true;
      }

    } else { // we have OS sent from App
      browser     = 'Mobile App';
      os          = osFromApp;
      mobile      = true;
    }

    return {
      'browser': browser,
      'os':      os,
      'isMobile':  mobile
    };
  }

}
