import { Component, forwardRef, Input } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";
import { RecordType } from '../../enums/record-type';

@Component({
  selector: 'v-record-type',
  template: `
    <button class="v-record-type" (click)="writeValue(!selected)" type="button" (ngModel)="selected" [ngClass]="[selected ? 'v-record-type--selected' : '', isTall ? 'v-record-type--tall' : '', 'v-record-type--' + type]">
        <v-icon [icon]="icon"></v-icon>
        <div [ngClass]="[isTall ? 'v-record-type__title--large' :'v-record-type__title']" [translate]="'component.record-type.' + type"></div>
    </button>
  `,
  providers: [
    {provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => RecordTypeComponent), multi: true}
  ],
  styles: [`.v-record-type{
  border:solid 1px #fff;
  -webkit-box-shadow:0 2px 3px rgba(100, 100, 100, 0.2);
          box-shadow:0 2px 3px rgba(100, 100, 100, 0.2);
  background:#fff;
  border-radius:.6rem;
  padding:1.5rem .5rem;
  text-align:center;
  width:100%; }
  @supports (-moz-appearance: none){
    .v-record-type:hover, .v-record-type:focus{
      cursor:pointer;
      -webkit-box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3);
              box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3); } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    .v-record-type:hover, .v-record-type:focus{
      cursor:pointer;
      -webkit-box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3);
              box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3); } }
  @media (hover: hover){
    .v-record-type:hover, .v-record-type:focus{
      cursor:pointer;
      -webkit-box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3);
              box-shadow:0 1px 8px 1px rgba(0, 0, 0, 0.3); } }
  .v-record-type v-icon{
    font-size:2rem;
    line-height:1;
    margin-bottom:.7rem; }
  .v-record-type .v-record-type__title{
    overflow:hidden;
    text-overflow:ellipsis;
    white-space:nowrap;
    font:600 1.2rem/2rem "Open Sans";
    line-height:2.4rem;
    margin-top:.7rem; }
  .v-record-type .v-record-type__title--large{
    font:600 1.6rem/2.4rem "Open Sans"; }
  .v-record-type.v-record-type--condition{
    color:#d20962; }
  .v-record-type.v-record-type--medication{
    color:#7d3f98; }
  .v-record-type.v-record-type--allergy{
    color:#d20962; }
  .v-record-type.v-record-type--surgery{
    color:#414141; }
  .v-record-type.v-record-type--family-history{
    color:#7d3f98; }
  .v-record-type.v-record-type--document{
    color:#414141; }
  .v-record-type.v-record-type--appointment{
    color:#7d3f98; }
  .v-record-type.v-record-type--tall{
    padding:3.65rem .5rem; }
  .v-record-type.v-record-type--selected{
    border-color:currentColor; }
`]
})
export class RecordTypeComponent implements ControlValueAccessor {
  selected: boolean;
  onChange: (selected: boolean) => void;
  onTouched: () => void;

  @Input() type: RecordType;
  @Input() isTall: boolean;


  get icon(): string {
    switch (this.type) {
      case RecordType.CONDITION:
        return 'heart-rate';
      case RecordType.MEDICATION:
        return 'pill';
      case RecordType.SURGERY:
        return 'surgeon';
      case RecordType.ALLERGY:
        return 'allergy';
      case RecordType.HISTORY:
        return 'familyhistoryshort';
      case RecordType.DOCUMENT:
        return 'document';
      case RecordType.APPOINTMENT:
        return 'calendar';
      default:
        return null;
    }
  }



  writeValue(value: boolean): void {
    if (this.onChange) {
      this.selected = value;
      this.onChange(this.selected);
    } 
  }

  registerOnChange(fn: () => {}): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }


}
