import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import moment from 'moment-es6';
import { Observable } from 'rxjs/Observable';
import { flatMap, map } from 'rxjs/operators';
import { DependentRelationship } from '../../enums/dependent-relationship';
import { VDependent } from "../../model/vDependent";
import { ConfigService, Session } from '../config/config.service';
import { VActivityResponse } from "../vActivityResponse";
import { VDependentResponse } from "./vDependentResponse";
import { VEligibilityResponse } from './vEligibilityResponse';
import { VInvitePolicyHolderRequest } from './vInvitePolicyHolderRequest';
import { VMemberContact, VMemberResponse, VMemberState } from './vMemberResponse';
import { VMemberUpdateRequest } from './vMemberUpdateRequest';
import { VPushConfig } from './vPushConfig';

export * from './vDependentResponse';
export * from './vMemberResponse';
export * from './vMemberUpdateRequest';
export * from './vPushConfig';
export interface PublicToken {
  access_token: string;

}
@Injectable()
export class MemberService {

  private readonly FETCH_API: string = '/members/';
  private permissionToken: PublicToken;

  constructor(private http: HttpClient, private config: ConfigService) {
  }

  /**
   * This API to fetch the family members info along with consent details.
   */
  getFamily(): Observable<VDependent[]> {
    const id = Session.INDIVIDUAL_ID;
    return this.http.get<{ familyGroup: VDependentResponse[] }>(`${this.config.get.apiUrl}/members/${id}/family`)
      .pipe(map(response => {
        //Sort Self first
        if (response.familyGroup.length > 1) {
          const index = response.familyGroup.findIndex(elem => elem.relationship === DependentRelationship.SELF);
          const firstElem = response.familyGroup[index];
          response.familyGroup.splice(index, 1);
          response.familyGroup.unshift(firstElem);
        }
        return response.familyGroup
          .map(dep => ({ id: dep.individualId, firstName: dep.firstName, lastName: dep.lastName, registered: dep.registered, consent: dep.consent, relationship: dep.relationship, dob: new Date(dep.dob) }));
      }
      ));
  }

  /**
   * This API to fetch the dependents Info along with consent details.
   */
  getDependents(): Observable<VDependent[]> {
    return this.getFamily().pipe(map(response => {
      const ageLimit = moment().subtract(18, 'years');
      return response.filter(dependent => ageLimit.isBefore(dependent.dob) || dependent.relationship === DependentRelationship.SELF);
    }));
  }

  /**
   * This API fetches the Member, Dependent and Policy details.
   */
  getMemberDetails(memberId: string): Observable<VMemberResponse> {
    return this.http.get<VMemberResponse>(`${this.config.get.apiUrl}${this.FETCH_API}${memberId}`);
  }
  /**
   * This API fetches the Member state.
   */
  getMemberState(): Observable<VMemberState> {
    return this.http.post<VMemberState>(`${this.config.get.apiUrl}/membercurrentstate/fetch`, null);
  }
  /**
   * This is to update the member contact details for both subscriber and dependent.
   * Only contact information can be updated i.e. email, country, city, countryPhoneCode, phone number.
   */
  getEmergencyContacts(individualId: string): Observable<VMemberContact[]> {
    return this.http.get<{ emergencyContacts: VMemberContact[] }>(`${this.config.get.apiUrl}/members/${individualId}/emergencycontacts`)
      .pipe(map(response => response.emergencyContacts));
  }
  /**
   * This API adds Emergency Contacts
   */
  addEmergencyContacts(params: VMemberContact[], individualId: string): Observable<VMemberContact[]> {
    return this.http.post<{ emergencyContacts: VMemberContact[] }>(`${this.config.get.apiUrl}/members/${individualId}/emergencycontacts`, JSON.stringify({ emergencyContacts: params }))
      .pipe(map(response => response.emergencyContacts));
  }

  /**
   * This API to tag the device in Azure Notification Hub.
   */
  registerPushToken(config: VPushConfig): Observable<VActivityResponse> {
    return this.http.post<VActivityResponse>(`${this.config.get.mockUrl}/pushnotification/devicetagging`, config);
  }

  /**
   * This is to update the member contact details for both subscriber and dependent.
   * Only contact information can be updated i.e. email, country, city, countryPhoneCode, phone number.
   */
  updateMemberDetails(memberId: string, params: VMemberUpdateRequest): Observable<VActivityResponse> {
    return this.http.post<VActivityResponse>(`${this.config.get.apiUrl}/members/${memberId}/contactdetails`, JSON.stringify(params));
  }


  checkEligibility(username: string, individualId: string): Observable<boolean> {
    if (!this.permissionToken) {
      return this.getPublicToken().pipe(flatMap(res => {
        const httpOptions = {
          headers: new HttpHeaders({
            'Authorization': res.access_token
          })
        };
        return this.http.post<VEligibilityResponse>(`${this.config.get.mockUrl}/eligibility`, { username, individualId }, httpOptions)
          .pipe(
            map(res => res.eligible)
          );
      }));

    }
    else {
      const httpOptions = {
        headers: new HttpHeaders({
          'Authorization': this.permissionToken.access_token
        })
      };


      // return this.http.get<VEligibilityResponse>(`${this.config.get.apiUrl}/${individualId}/features?username=${username}`)
      return this.http.post<VEligibilityResponse>(`${this.config.get.mockUrl}/eligibility`, { username, individualId }, httpOptions)
        .pipe(
          map(res => res.eligible)
        );
    }
  }
  getPublicToken(): Observable<PublicToken> {
    return this.http.get<PublicToken>(`${this.config.get.permissionUrl}`)
      .pipe(map(response => {

        this.permissionToken = response;
        return this.permissionToken;
      }));
  }


  invitePolicyHolders(params: VInvitePolicyHolderRequest): Observable<VActivityResponse> {
    const httpOptions = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      })
    };
    return this.http.post<VActivityResponse>(`${this.config.get.inviteUrl}`, JSON.stringify(params), httpOptions);
  }

}
