import { HttpClient } from "@angular/common/http";
import { Injectable } from '@angular/core';
import { Observable } from "rxjs/Observable";
import { of } from "rxjs/observable/of";
import { map } from "rxjs/operators";
import { VDependent } from "../../model/vDependent";
import { ConfigService } from "../config/config.service";
import { VActivityResponse } from "../vActivityResponse";
import { allergies } from "./allergies";
import { VAllergy, VAllergyType } from "./vAllergy";

@Injectable()
export class AllergyService {


  allergyTypes: Array<{key: string, value: string}> = [
    { key: 'Env', value: 'Environmental' },
    { key: 'env', value: 'Environmental' },
    { key: 'Food', value: 'Food' },
    { key: 'Med', value: 'Medication' }
  ];

  constructor(private http: HttpClient, private config: ConfigService) {
  }

  private convert(response: VAllergy): VAllergy {

    const allergies: VAllergy = {
      allergyId: response.allergyId,
      allergyName: response.allergyName,
      allergyNotes: response.allergyNotes,
      allergyType: this.convertType(response.allergyType)
    };

    return allergies;
  }

  convertType(type: string):string {
    const typeConverted = this.allergyTypes.find(res => res.key === type);
    return typeConverted ? typeConverted.value : '';
  }

  /**
   * Fetch all allergies for an individual
   */
  fetchAll(individualId: string): Observable<VAllergy[]> {
    return this.http.get<{ allergies: VAllergy[] }>(`${this.config.get.apiUrl}/vhealth_members/${individualId}/allergies`)
      .pipe(map(response => response.allergies.map(allergy => this.convert(allergy))));
  }

  /**
   * Add or update an allergy.
   */
  save(allergy: VAllergy, dependent: VDependent): Observable<VAllergy> {

    const params = {
      individualId: dependent.id,
      allergyName: allergy.allergyName,
      allergyNotes: allergy.allergyNotes,
      allergyId: '',
      activity: ''
    };

    if (allergy.allergyId) {
      params.activity = 'UPDATE';
      params.allergyId = allergy.allergyId;
      return this.http.put<VAllergy>(`${this.config.get.apiUrl}/vhealth_allergies/${params.allergyId}`, JSON.stringify(params))
      .pipe(map(response => response.allergyId ? response : allergy));
    } else {
      params.activity = 'ADD';
      delete params.allergyId;
      return this.http.post<VAllergy>(`${this.config.get.apiUrl}/vhealth_members/${params.individualId}/allergies`, JSON.stringify(params))
      .pipe(map(response => response.allergyId ? response : allergy));
    }
  }

  /**
   * Remove an allergy
   */
  remove(allergyId: string): Observable<VActivityResponse> {
    return this.http.delete<VActivityResponse>(`${this.config.get.apiUrl}/vhealth_allergies/${allergyId}`);
  }

  /**
   * Return all allergy types
   */
  types(): Observable<VAllergyType[]> {

    return this.http.post<{allergies: VAllergyType[]}>(`${this.config.get.apiUrl}/allergies/types/fetch`, null)
      .pipe(map(response => response.allergies));
  }

  /**
   * Return a pre-defined list of conditions for use in autofill etc.
   */
  lookup(keyword:string): Observable<string[]> {
    return of(allergies.filter(res=> res.toLowerCase().match(keyword.toLowerCase())));
  }
}
