import { Component, forwardRef, Input } from "@angular/core";
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from "@angular/forms";

@Component({
  selector: "v-option-button",
  template: `
    <button class="option-button"
            type="button"
            [ngClass]="{'option-button--selected': isSelected}"
            (click)="writeValue(value)">
      <ng-content></ng-content>
    </button>

  `,
  providers: [
    {provide: NG_VALUE_ACCESSOR, useExisting: forwardRef(() => OptionButtonComponent), multi: true}
  ],
  styles: [`.option-button{
  background:#fff;
  border:1px solid #fff;
  border-radius:6px;
  color:inherit;
  cursor:pointer;
  text-align:left;
  font:1.6rem/2.4rem "Open Sans";
  padding:1.7rem;
  white-space:nowrap;
  overflow:hidden;
  width:100%;
  outline:none;
  -webkit-transition:border-color 280ms ease-in-out;
  transition:border-color 280ms ease-in-out; }
  @supports (-moz-appearance: none){
    .option-button:hover, .option-button:focus{
      border-color:#d20962; } }
  @media all and (-ms-high-contrast: active), (-ms-high-contrast: none){
    .option-button:hover, .option-button:focus{
      border-color:#d20962; } }
  @media (hover: hover){
    .option-button:hover, .option-button:focus{
      border-color:#d20962; } }

.option-button--selected{
  color:#d20962;
  outline:0;
  border-color:#d20962; }
`]
})
export class OptionButtonComponent implements ControlValueAccessor {

  private selected: object;

  @Input() icon: string;
  @Input() value: object;

  onChange: (value: object) => void;
  onTouched: () => void;

  get isSelected(): boolean {
    return this.value && this.selected === this.value;
  }

  writeValue(value: object): void {
    if (this.onChange) {
      this.selected = value;
      this.onChange(value);
    }
  }

  registerOnChange(fn: () => {}): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => {}): void {
    this.onTouched = fn;
  }

}
