import { Injectable } from '@angular/core';
import { Moment, utc } from 'moment';
import { ConfigService } from '../services/config/config.service';

@Injectable()
export class DateUtil {

  constructor (private config: ConfigService) {
  }

  static readonly FORMAT: string = 'YYYY-MM-DD HH:mm Z';
  static readonly SHORT_FORMAT: string = 'YYYY-MM-DD HH:mm';

  /**
   * Format a date
   *
   * @param date momentizable date object
   * @param format moment date format
   */
  format(date: Date | Moment | string | number, format: string = 'll'): string {
    return utc(date).isValid() ? utc(date).locale(this.config.get.locale).local().format(format) : null;
  }

  /**
   * Formats a date into the API pattern.
   *
   * @param date date object
   */
  formatAPIDate(date: Date): string {
    return utc(date).format(DateUtil.FORMAT);
  }
  /**
   * Formats a date into the API pattern without timezone.
   *
   * @param date date object
   */
  formatShortAPIDate(date: Date): string {
    return utc(date).format(DateUtil.SHORT_FORMAT);
  }
  /**
   * Use moment to convert an api format date into a valid
   * ISO date string.
   *
   * @param vDate api format date string
   */
  convertAPIDateToString(vDate: string): string {
    return this.convertAPIDate(vDate).toISOString();
  }

  /**
   * Use moment to convert an api format date into a valid
   * ISO date string.
   *
   * @param vDate api format date string
   */
  convertAPIDate(vDate: string): Date {
    return utc(vDate, DateUtil.FORMAT).local().toDate();
  }
  /**
   * Use moment to convert an api format date into a valid
   * ISO date string without timezone.
   *
   * @param vDate api format date string
   */
  convertShortAPIDate(vDate: string): Date {
    return utc(vDate, DateUtil.SHORT_FORMAT).local().toDate();
  }

  /**
   * Round the hour to the closest 00 or 30 minutes
   *
   * @param date date to be rounded
   */
  roundTime(date: Date):Date{
    const remainder = 30 - (date.getMinutes() % 30);
    return new Date(utc(date).add(remainder, 'minutes').toString());
  }



}
