import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable, Renderer2, RendererFactory2 } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { flatMap, map } from 'rxjs/operators';
import { PublicToken } from '../../public_api';
import { ConfigService } from '../config/config.service';
import { VFAQ } from './vFAQ';

@Injectable()
export class FAQService {

  private renderer: Renderer2;
  private publicToken: PublicToken;
  constructor(private http: HttpClient, private config: ConfigService, rendererFactory: RendererFactory2) {
    this.renderer = rendererFactory.createRenderer(null, null);
  }

  private convertFAQs(html: string): VFAQ[] {
    const faqs: VFAQ[] = [];

    const element = this.renderer.createElement('html') as HTMLElement;
    element.innerHTML = html;

    const questions = element.querySelectorAll('h2');
    for (let i = 0; i < questions.length; i++) {
      const question: HTMLElement = questions[i];
      faqs.push({ question: question.innerHTML, answer: question.nextElementSibling.innerHTML });
    }

    return faqs;
  }

  /**
   * List the FAQs and convert them from the HTML string into a usable format.
   */
  getFAQs(): Observable<VFAQ[]> {
    if (!this.publicToken) {
      return this.getPublicToken().pipe(flatMap(res => {
        const httpOptions = {
          headers: new HttpHeaders({
            'Authorization': res.access_token
          })
        };
        return this.http.get<{ content: string }>(`${this.config.get.apiUrl}/vhealth_faqs`, httpOptions)
          .pipe(map(({ content }) => this.convertFAQs(content)));

      }));
    
    }
    else {
      const httpOptions = {
        headers: new HttpHeaders({
          'Authorization': this.publicToken.access_token
        })
      };
      return this.http.get<{ content: string }>(`${this.config.get.apiUrl}/vhealth_faqs`, httpOptions)
        .pipe(map(({ content }) => this.convertFAQs(content)));
    }
  }


  getPublicToken(): Observable<PublicToken> {
    return this.http.get<PublicToken>(`${this.config.get.publicUrl}`)
      .pipe(map(response => {
        this.publicToken = response;
        return this.publicToken;
      }));
  }

}
