import { InjectionToken } from '@angular/core';
import { updateLocale } from 'moment';
import { VDependent } from '../../model/vDependent';
import { StoreFactory } from './storage/object-store.factory';
import { ObjectStore, StoreID } from './storage/object-store.interface';

/**
 * Configuration options
 */
export interface Config {
  apiUrl?: string;
  authUrl?: string;
  consultationUrl?: string;
  inviteUrl?: string;
  mockUrl?: string;
  publicUrl?:string;
  permissionUrl?:string;
  outboundSsoUrl?: string;
  locale?: string;
  translationPrefix?: string;
  translationSuffix?: string;
}

/**
 * Session information. This data is cleared when a user
 * is logged out.
 */
export class Session {
  static DEPENDENTS: VDependent[] = [];
  static ACTIVE_DEPENDENT: VDependent = null;
  static INDIVIDUAL_ID: string = null;
  static HEALTHKIT_PERM_ALLOWED: boolean = false;
}

export class ConfigService {

  static readonly DEFAULTS: Config = {
    apiUrl: 'api:',
    authUrl: 'auth:',
    inviteUrl: 'invite:',
    consultationUrl: 'consult:',
    mockUrl: 'mock:',
    publicUrl:'public:',
    permissionUrl:'permission:',
    outboundSsoUrl: 'outbound:',
    translationPrefix: '/assets/i18n',
    translationSuffix: 'json',
  };

  private config: Config;
  private store: ObjectStore;

  constructor(config: Config) {
    this.update(config);
    this.store = StoreFactory.load(StoreID.CONFIG);
  }


  /**
   * Get the current config
   */
  get get(): Config {
    return Object.assign({}, this.config);
  }

  /**
   * Update the config fields
   */
  update(config: Config): void {
    this.config = Object.assign({}, ConfigService.DEFAULTS, this.config, config);

    updateLocale(this.config.locale, {
      longDateFormat: {
        LT: "h:mm a",
        LTS: "h:mm:ss a",
        L: "DD/MM/YYYY",
        LL: "D MMMM YYYY",
        LLL: "D MMMM LT",
        LLLL: "dddd D MMMM [at] LT"
      }
    });
  }

  /**
   * Clear one or all values in storage
   */
  clearData(key?: string): Promise<void> {
    return this.store.clear(key);
  }

  /**
   * Retrieve an item from storage
   */
  getData<T>(key: string): Promise<T> {
    return this.store.get<T>(key);
  }

  /**
   * Save an item into storage
   */
  setData(key: string, data: string | number | boolean | object): Promise<void> {
    return this.store.put(key, data);
  }

  /**
   * Update the current user used by the config store.
   */
  updateUser(userId: string): void {
    this.store.user = userId;
  }

}

export const CONFIG_TOKEN = new InjectionToken<Config>('CommonConfig');
