import { Component, Input } from "@angular/core";

@Component({
  selector: "v-progress",
  template: `
    <ol class="v-progress" role="progressbar" [attr.aria-valuenow]="step" aria-valuemin="0" [attr.aria-valuemax]="total">
      <li class="v-progress__item" [ngClass]="'v-progress__item--' + theme" [attr.active]="step === progress" *ngFor="let progress of steps"></li>
    </ol>
  `,
  styles: [`.v-progress{
  color:inherit;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  list-style:none;
  margin:0;
  padding:0; }

.v-progress__item{
  margin:0 .2rem; }
  .v-progress__item:before{
    background:#dcdcdc;
    border-radius:50%;
    content:'';
    -webkit-transition:background 480ms ease-in-out;
    transition:background 480ms ease-in-out;
    display:block;
    width:.6rem;
    height:.6rem; }
  .v-progress__item--standard[active='true']:before{
    background:#414141; }
  .v-progress__item--teal[active='true']:before{
    background:#00f0cc; }
`]
})
export class ProgressComponent {

  @Input() step: number = 1;
  @Input() total: number = 1;
  @Input() theme: string = 'standard';

  get steps(): number[] {
    const steps = [];
    for (let i = 1; i <= this.total; i++) {
      steps.push(i);
    }
    return steps;
  }
}
